import React, {useEffect} from 'react';
import './App.css';
import AccountBalance from './components/AccountBalance/AccountBalance';
import CoinList from './components/CoinList/CoinList';
import AppHeader from './components/AppHeader/AppHeader';
import axios from 'axios';


const COIN_COUNT = 10;
const coinsUrl = 'https://api.coinpaprika.com/v1/coins';
const tickerUrl = 'https://api.coinpaprika.com/v1/tickers/';

function App(props) {

  const [balance, setBalance] = React.useState(50000);
  const [showBalance, setShowBalance] = React.useState(true);
  const [coinData, setCoinData] = React.useState([]);
  
  const componentDidMount = async () => {
    const response = await axios.get(coinsUrl)
      const coinIds = response.data.slice(0, COIN_COUNT).map(coin => coin.id);
      const promises = coinIds.map(id => axios.get(tickerUrl + id));
      const coinData = await Promise.all(promises);
  
      const coinPriceData = coinData.map(function(response){
        const coin = response.data;
        const url_graph_news = 'https://coinmarketcap.com/currencies/' + coin.name + '?period=7d';

        if(coin.quotes["USD"].percent_change_30m.toString().charAt(0) === '-'){
          return {
            rank: coin.rank,
            key: coin.id,
            name: <a href={url_graph_news}>{coin.name}</a>,
            ticker: coin.symbol,
            balance: 0, 
            percent_change_30m: <p className="text-right minus_price"><span className='icon_minus'></span>{coin.quotes["USD"].percent_change_30m}%</p>,
            percent_change_1h: <p className='text-right minus_price'><span className='icon_minus'></span>{coin.quotes["USD"].percent_change_1h}%</p>, 
            price: parseFloat(Number(coin.quotes["USD"].price)).toFixed(3)
          };
        }else{
          return {
            rank: coin.rank,
            key: coin.id,
            name: <a href={url_graph_news}>{coin.name}</a>,
            ticker: coin.symbol,
            balance: 0, 
            percent_change_30m: <p className='text-right plus_price'><span className='icon_plus'></span>{coin.quotes["USD"].percent_change_30m}%</p>,
            percent_change_1h: <p className='text-right plus_price'><span className='icon_plus'></span>{coin.quotes["USD"].percent_change_1h}%</p>, 
            price: parseFloat(Number(coin.quotes["USD"].price)).toFixed(3)
          };
        }
      }) 
      setCoinData(coinPriceData);
  }

  useEffect(function(){
    if(coinData.length === 0){
      componentDidMount();
    }
  });
  
  const handleRefresh = async (valueChangeTicker) => {
   const keyData =  await axios.get( tickerUrl + valueChangeTicker);
   const newCoinData = coinData.map(function( values ) {
    let newValues = {...values};
    if(values.key === valueChangeTicker){
      console.log(newValues);
        newValues.price =  parseFloat(Number(keyData.data.quotes["USD"].price)).toFixed(3)
      }
      return newValues;
    });
    // this.setState({coinData: newCoinData});
    setCoinData(newCoinData);

  }

  const buyCoin = (valueChangeTicker) => {
    const newCoinData = coinData.map(function( values ) {
      let newValues = {...values};
      if(values.key === valueChangeTicker){
          newValues.balance += 1
          setBalance(balance - Number(newValues.price));
        }
        return newValues;
      });
      setCoinData(newCoinData);
  }

  const sellCoin = (valueChangeTicker) => {
    const newCoinData = coinData.map(function( values ) {
      let newValues = {...values};
      if(values.key === valueChangeTicker){
          newValues.balance -= 1
          setBalance(balance + Number(newValues.price));
        }
        return newValues;
      });
      setCoinData(newCoinData);
  }

  
  const handleBalance = () => {
    setShowBalance(oldValue => !oldValue);
  }

  const handleAddBalance = () => {
    setBalance( balance + 1200);
  }

  return (
  <div className="App">
      <AppHeader />
      
      <AccountBalance 
      amount={balance} 
      handleBalance={handleBalance}
      handleAddBalance={handleAddBalance} 
      showBalance={showBalance}
      />
      
      <CoinList 
      coinData={coinData} 
      showBalance={!showBalance } 
      handleRefresh={handleRefresh} 
      buyCoin={buyCoin}
      sellCoin={sellCoin} 
      />
  
  </div>
  ); 
}

export default App;
