import React from 'react'
import PropTypes from 'prop-types';
import './AccountBalance.css';

export default function AccountBalance(props) {

    const buttonText = props.showBalance 
    ? 'Show Balance' : 'Hide Balance' ;

    let balance = props.showBalance ? '' : 
    <div className='balance_text'>
      Balance: ${props.amount}
        <button className="balance_btn" onClick={props.handleAddBalance} >Add $</button> 
      </div>;

    return (
      <section>
        {balance}
        <button className="balance_btn" onClick={props.handleBalance} >{buttonText}</button> 
      </section>
    );
}

AccountBalance.propTypes = {
    amount: PropTypes.number.isRequired,
}