import React from 'react'
import './Coin.css';
import refresh_logo_btn from '../../refresh_button.svg';
import PropTypes from 'prop-types';


export default function Coin(props)  {
  const handleClick = (event) => {
    event.preventDefault();
    props.handleRefresh(props.id);
  }

  const handleClickBuy = (event) => {
    props.buyCoin(props.id);
  }

  const handleClickSell = (event) => {
    props.sellCoin(props.id);

  }

    return (
      <tr className='coin-row'>
      <td><p>{props.rank}</p></td>
      <td><p className='text-left'>{props.name} {props.ticker}</p></td>
      <td><p className='text-right'>${props.price}</p></td>
      <td>{props.percent_change_30m}</td> 
      <td>{props.percent_change_1h}</td> 
      {props.showBalance ? <><td>{props.balance}</td></> : '' }
      
      <td>
      { props.showBalance ? <button className="btn_buy" onClick={handleClickBuy}>BUY</button> : '' }
          <button className="btn_refresh" onClick={handleClick}> <img src={refresh_logo_btn} className='logo_refresh' /> </button>
      { props.showBalance ? <button className="btn_sell" onClick={handleClickSell}>SELL</button> : '' }
      </td>
    </tr>
    );
}

Coin.propTypes = {
  name: PropTypes.string.isRequired,
  ticker: PropTypes.string.isRequired,
  price: PropTypes.string.isRequired,
}