import React from 'react'
import Coin from '../Coin/Coin';

export default function CoinList(props) {
    return (
        <table className='coin-table'>
        <thead>
          <tr>
            <th>#</th>
            <th className='th-letf'>Name</th>
            <th className='text-right'>Price</th>
            <th className='text-right'>30m %</th>
            <th className='text-right'>1h %</th>
            { props.showBalance ? <th>Balance</th> : '' }
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
        {
          props.coinData.map(({rank, key, name, ticker, percent_change_30m, percent_change_1h, balance, price}) =>
            <Coin 
            rank={rank} 
            key={key} 
            id={key}
            handleRefresh={props.handleRefresh}
            buyCoin={props.buyCoin}
            sellCoin={props.sellCoin} 
            name={name} 
            ticker={ticker}
            percent_change_30m={percent_change_30m}
            percent_change_1h={percent_change_1h}
            showBalance={props.showBalance}
            balance={balance} 
            price={price} />
              )
        }
        </tbody>
        </table>
    )
}
